#header {
  width: 100%;
  height: 70px;
  background-color: white;
  margin: 0 auto;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
}

#logo {
  width: 5%;
  height: 60%;
  line-height: 4;
}

@media (min-width: 1920px) {
  #header {
    height: 90px;
  }
  #logo {
    line-height: 5;
  }
}
